import React, { Component } from "react";

import "./App.css";
import axios from "axios";
import "bootstrap/dist/css/bootstrap.min.css";

import { Modal, ModalBody, ModalFooter, ModalHeader } from "reactstrap";
import O from "./App3";
import Validar from "./validar";


// url y sacamos los datos en motodo json
const url = "https://testing-pay-license.usound.co/payments/api/api/clients/";

class App extends Component {
  state = {
    data: [],
    modalInsertar: false,
    form: {
      id: "",
      timestamp: "",
      nombre: "",
      email: "",
      phone: "",
      razon_social: "",
      identifier: "",
      country: "",
      type_id: "",
      iva: "",
      type: "",
      person: "empty",
      vat_number: "",
      carnet_id: "",
      phone_country_code: "",
      phone_area_code: "",
      phone_work_country_code: "",
      phone_work_area_code: "",
      phone_work: "",
    },
  };
 // peticion  para agregar los datos 
  peticionPost = async () => {
    delete this.state.form.id;
    await axios
      .post(url, this.state.form)
      .then((response) => {
        this.modalInsertar();
        this.peticionGet();
      })
      .catch((error) => {
        console.log(error.message);
      });
  };
 // insertacion de los datos 
  modalInsertar = () => {
    this.setState({ modalInsertar: !this.state.modalInsertar });
  };
// una copia de los datos
  handleChange = async (e) => {
    e.persist();
    await this.setState({
      form: {
        ...this.state.form,
        [e.target.name]: e.target.value,
      },
    });
    console.log(this.state.form);
  };

  

  render() {
    const { form } = this.state;
      
    return (
      
      
      <div className="App">
        
   
       {/* // tomando botones con iconos  */}
        <button 
        className="btn btn-primary" onClick={() => this.modalInsertar()}>
          <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" class="bi bi-person-plus-fill" viewBox="0 0 16 16">
  <path d="M1 14s-1 0-1-1 1-4 6-4 6 3 6 4-1 1-1 1H1zm5-6a3 3 0 1 0 0-6 3 3 0 0 0 0 6z"/>
  <path fill-rule="evenodd" d="M13.5 5a.5.5 0 0 1 .5.5V7h1.5a.5.5 0 0 1 0 1H14v1.5a.5.5 0 0 1-1 0V8h-1.5a.5.5 0 0 1 0-1H13V5.5a.5.5 0 0 1 .5-.5z"/>
</svg>
{/* //espacios  */}
&nbsp;&nbsp;&nbsp;Agregar&nbsp;&nbsp;&nbsp;  

        
     
        </button>
        {/* // llamando mi table buscar  O */}
        <O/>
        {/* // formulario para agregar  */}
        <Modal isOpen={this.state.modalInsertar}>

          <ModalHeader style={{ display: "block" }}>
         
            <span
              style={{ float: "right" }}
              onClick={() => this.modalInsertar()}
            >
              x
            </span>
            <center>Cliente Personales</center>
          </ModalHeader>
         
          <ModalBody response needs-validation>
            <div className="form-group"  needs-validation>
              <label htmlFor="id">#</label>
              <input
                className="form-control"
                type="text"
                name="id"
                id="id"
                readOnly
                onChange={this.handleChange}
                value={this.state.data.length + 1}
              />
              <br />

              <div class="form-group">
                <label htmlFor="type_id">Tipo de Documento2</label>

                <div class="col-sm-10">
                  <select
                    class="form-control"
                    className="form-control"
                    type="text"
                    name="type_id"
                    id="type_id"
                    onChange={this.handleChange}
                    value={form.type_id}
                  >
                    <option value="" disabled selected>
                      --------
                    </option>

                    <option value="DNI">DNI</option>

                    <option value="CUIT">CUIT</option>

                    <option value="CUIL">CUIL</option>

                    <option value="DU">DU</option>
                  </select>
                </div>
              </div>
              <br />
              <br />
              <label htmlFor="identifier">Num de documento</label>
              <input
                className="form-control"
                type="text"
                name="identifier"
                id="identifier"
                onChange={this.handleChange}
                value={form.identifier}
              />
              <br />
              <label htmlFor="nombre">Nombre y Apellido</label>
              <input
                className="form-control"
                type="text"
                name="nombre"
                id="nombre"
                onChange={this.handleChange}
                value={form.nombre}
              />
              <br />
              <br />
              <label htmlFor="razon_social">Razon Social</label>
              <input
                className="form-control"
                type="text"
                name="razon_social"
                id="razon_social"
                onChange={this.handleChange}
                value={form.razon_social}
              />
              <br />
              <br />
              <label htmlFor="vat_number">Cuit/Cuil/Rut</label>
              <input
                className="form-control"
                type="Number"
                name="vat_number"
                id="vat_number"
                onChange={this.handleChange}
                value={form.vat_number}
              />
              <br />

              <div class="form-group">
                <label htmlFor="iva">CondiciÃ³n Fiscal</label>

                <div class="col-sm-10">
                  <select
                    class="form-control"
                    className="form-control"
                    type="text"
                    name="iva"
                    id="iva"
                    onChange={this.handleChange}
                    value={form.iva}
                  >
                    <option value="empty">empty</option>

                    <option value="consumidor final">consumidor final</option>

                    <option value="responsable inscripto">
                      responsable inscripto
                    </option>

                    <option value="exento">exento</option>

                    <option value="monotributista">monotributista</option>
                  </select>
                </div>
              </div>
              <br />
              <br />

              <div class="form-group">
                <label htmlFor="country">PAIS</label>

                <div class="col-sm-10">
                  <select
                    class="form-control"
                    className="form-control"
                    type="text"
                    name="country"
                    id="country"
                    onChange={this.handleChange}
                    value={form.country}
                  >
                    <option value="AR">AR</option>

                    <option value="CO">CO</option>
                  </select>
                </div>
              </div>
              <br />
              <label htmlFor="carnet_id">Num. de Matricula</label>
              <input
                className="form-control"
                type="Number"
                name="carnet_id"
                id="carnet_id"
                onChange={this.handleChange}
                value={form.carnet_id}
              />
              <br />
              <br />

              <div class="form-group">
                <label htmlFor="type">Tipo de Cliente</label>

                <div class="col-sm-10">
                  <select
                    class="form-control"
                    name="type"
                    className="form-control"
                    type="text"
                    id="type"
                    onChange={this.handleChange}
                    value={form.type}
                  >
                    <option value="Cliente Final">Cliente Final</option>

                    <option value="Distribuidor">Distribuidor</option>
                  </select>
                </div>
              </div>
              <br></br>
              <label htmlFor="">
            
            <center><h5>Cliente Personales</h5></center>
                ______________________________________________________________________
              </label>
              <br />
              <label htmlFor="email">Correo</label>
              <input
                className="form-control"
                type="email"
                name="email"
                id="email"
                onChange={this.handleChange}
                value={form.email}
                
                
              />
              <br />
              <label htmlFor="phone_country_code">Cod . Pais</label>
              <input
                className="form-control"
                type="Number"
                name="phone_country_code"
                id="phone_country_code"
                onChange={this.handleChange}
                value={form.phone_country_code}
              />
              <br />
              <br />
              <label htmlFor="phone_area_code">Cod area</label>
              <input
                className="form-control"
                type="Number"
                name="phone_area_code"
                id="phone_area_code"
                onChange={this.handleChange}
                value={form.phone_area_code}
              />
              <br />
              <br />
              <label htmlFor="phone">Tel. Personal</label>
              <input
                className="form-control"
                type="Number"
                name="phone"
                id="phone"
                onChange={this.handleChange}
                value={form.phone}
              />
              <br />
              <br />
              <label htmlFor="phone_work_country_code">Cod Pais</label>
              <input
                className="form-control"
                type="Number"
                maxLength="3"
                name="phone_work_country_code"
                id="phone_work_country_code"
                onChange={this.handleChange}
                value={form.phone_work_country_code}
              />
              <br />
              <br />
              <label htmlFor="phone_work_area_code">Cod area</label>
              <input
                className="form-control"
                type="Number"
                maxLength="3"
                name="phone_work_area_code"
                id="phone_work_area_code"
                onChange={this.handleChange}
                value={form.phone_work_area_code}
              />
              <br />
              <br />
              <label htmlFor="phone_work">Tel.Laboral</label>
              <input
                className="form-control"
                type="Number"
                name="phone_work"
                required
                id="phone_work"
                onChange={this.handleChange}
                value={form.phone_work}
              />
              <br />

              <br />
              
            </div>
            
          </ModalBody>

        {/* // botones para el formulario  */}
          <ModalFooter>
          
            <button
              className="btn btn-success"
              onClick={() => this.peticionPost()
              }
            >
              Guardar
            </button>

            <button
              className="btn btn-danger"
              onClick={() => this.modalInsertar()}
            >
              Cancelar
            </button>
          </ModalFooter>
              
        </Modal>
      
      </div>
      
    );
   
  }
}

export default App;

// Example starter JavaScript for disabling form submissions if there are invalid fields
